@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@100;200;300;400;500;600&display=swap');

.filter {
  display: flex;
  align-items: center;
  justify-content: center;

  & i {
    margin-left: 0.5rem;
    margin-right: 0.5rem;
  }

  &__dropdown {
    box-sizing: border-box;
    padding: 0.3rem 0.5rem;
    letter-spacing: 1px;
    font-weight: 500;
    width: 150px;
    border-radius: var(--border-radius);
    user-select: none;
    position: relative;
  }

  &__btn {
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: space-between;
    user-select: none;
  }

  &__option {
    font-size: 0.9rem;
    margin-right: 0.1rem;
  }

  &__content {
    box-sizing: border-box;
    padding: 0rem 0.5rem;
    position: absolute;
    z-index: 1;
    top: 2rem;
    left: 0;
    width: 150px;
    border-radius: var(--border-radius);
    transform: translateY(100vh);
    transition: var(--transition);
  }

  &__item {
    margin: 0.6rem 0;
    padding: 0px 0px;
    cursor: pointer;
    font-size: 90%;

    span {
      display: block;
      padding: 0.2rem 0.5rem;
      border-radius: var(--border-radius);
      transition: var(--transition);
    }

    span:hover {
      background: #000;
      color: var(--tomato);
      box-shadow: none;
    }
  }
}

@media screen and (max-width: 1024px) {
  .filter {
    &__content {
      padding: 0rem 0.5rem;
    }

    &__item {
      margin: 0.5rem 0;
      padding: 0px 0px;
      cursor: pointer;
      font-size: 90%;

      span {
        display: block;
        padding: 0.2rem 0.5rem;
        border-radius: var(--border-radius);
        transition: var(--transition);
      }
    }
  }
}

@media screen and (max-width: 786px) {
  .dropdown {
    width: 100px;
  }
}

.darkShadow {
  box-shadow: 0 0 2px var(--dark-color-2);
  border: none;
}

.lightShadow {
  box-shadow: 0 0 2px var(--light-color-2);
  border: none;
}
